import Vue from 'vue'
import dayjs from 'dayjs'

// 加载中文语言
import 'dayjs/locale/zh-cn'

import relativeTime from 'dayjs/plugin/relativeTime'
// 配置使用相对时间的插件
dayjs.extend(relativeTime)

// dayjs默认语言是英文，我们配置为中文
dayjs.locale('zh-cn')

// console.log(dayjs().format('YYYY-MM-DD'))
// dayjs().from(dayjs('1990')) // 2 years ago
// dayjs().from(dayjs(), true) // 2 years

// dayjs().fromNow()

// dayjs().to(dayjs())

// dayjs().toNow()
// 全局过滤器 仅模板可用
Vue.filter('relativeTime', (val) => dayjs().to(dayjs(val)))
export const formatDate = (dataObj) => {
  return dayjs(dataObj).format('YYYY-MM-DD')
}
